/*========================================================== Imports ==========================================================*/

@import url('https://fonts.googleapis.com/css?family=Archivo+Black|Oxygen:300,400,700&display=swap');

/*========================================================== Mixins ==========================================================*/

%archivo {
	font-family: 'Archivo Black', sans-serif;
}

%oxygen-light {
	font-family: 'Oxygen', sans-serif;
	font-weight: 300;
	letter-spacing: .5px;
}

%oxygen-regular {
	font-family: 'Oxygen', sans-serif;
	font-weight: 400;
	letter-spacing: .5px;
}

%oxygen-bold {
	font-family: 'Oxygen', sans-serif;
	font-weight: 700;
	letter-spacing: .5px;
}

/*========================================================== Fonts ==========================================================*/

h1,
h2,
h3,
h4,
h5,
h6,
p {
	@extend %paMar;
}

h1 {
	@extend %archivo;
	font-size: 40px;
     text-align: center;
     text-transform: uppercase;
	letter-spacing: 5px;
	width: 100%;
	text-align: center;
}

h2,
.type-wrap {
     margin: 0;
     line-height: normal !important;
     font-family: 'Archivo Black', sans-serif;
     font-size: 35px;
     float: left;
     text-transform: uppercase;
	letter-spacing: 5px;
	width: 100%;
	text-align: center;
}

.type-wrap {
     letter-spacing: normal;
}

h3 {
     letter-spacing: 5px;
     text-transform: uppercase;
     font-size: 25px;
     margin-bottom: 1em;
     width: 100%;
     float: left;
}